.card {
  height: 18.92vw;
  width: 16.08vw;
  background: #1f1d2b;
  border-radius: 1.34vw;

  position: relative;
  transition: all 0.2s linear;
}
.card:hover {
  box-shadow: inset 0 0 0.6vw #ccc;
  transform: translateY(-0.3vw);
}

.card > .card_image > img {
  width: 11.05vw;
  object-fit: cover;
  position: absolute;
  top: -2.84vw;
  height: 11.05vw;
}
.Cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 4vw 0;
  overflow-y: scroll;
  margin-top: 4vw;

  height: 47vw;

  padding-top: 4.85vw;
}
.Cards::-webkit-scrollbar {
  width: 0.73vw;
  border-radius: 0.73vw;
}
.Cards::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0.41vw white;

  border-radius: 0.73vw;
}
.Cards::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 0.73vw;
}
.card > .card_name {
  display: flex;
  justify-content: center;
}
.card > .card_name > p {
  margin-top: 9.54vw;
  width: 12.06vw;

  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;

  text-align: center;

  color: #ffffff;
}
.card > .summa {
  margin-top: 0.67vw;
  color: #ffffff;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;
  line-height: 140%;
  text-align: center;
}
.card_image {
  display: flex;
  justify-content: center;
}
.card_quantity {
  text-align: center;
  font-style: normal;
  font-family: 'Barlow', sans-serif;
  font-weight: 400;
  font-size: 1.17vw;
  margin-top: 1vw;
  line-height: 140%;

  color: #abbbc2;
}
