.sidebar_logo {
  display: flex;
  padding-left: 1.8vw;
  padding-right: 1.8vw;

  flex-direction: column;
}
.sidebarlogo {
  display: flex;
  justify-content: center;
}
.sidebar_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5vw;
}

.sidebar_icon > img {
  width: 2.01vw;
  height: 2.01vw;
  object-fit: cover;
}
.sidebar {
  width: 7.5vw;
  height: 69.8vw;
  display: flex;

  flex-direction: column;
  background-color: #1f1d2b;
  border-radius: 0.83vw;
  padding-top: 2.01vw;
}
.logo {
  display: flex;
  width: 4.69vw;
  height: 4.69vw;
  background-color: rgb(176, 154, 142);
  justify-content: center;
  align-items: center;

  border-radius: 1vw;
}
.logo > img {
  width: 3.35vw;
  height: 3.35vw;
}

.sadebar_button {
  margin-top: 12vw !important;
}
.sadebar_link.active > .sidebar_icon {
  background: #ea7c69;
  box-shadow: 0 0.67vw 2.01vw rgba(234, 124, 105, 0.32);
  border-radius: 0.67vw;
  width: 4.69vw;
  position: relative;
  left: -0.5vw;
  height: 4.69vw;
}
.sadebar_link.active > .sidebar_icon > svg path {
  fill: aliceblue;
}
