.dashboroat_order {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3.76vw;
  padding: 2.01vw;
}
.dashboroat_order > h2 {
  font-style: normal;
  font-weight: 600;
  font-size: 1.67vw;
  color: white;
}
.dashboroat_order > div {
  display: flex;
  align-items: center;
  width: 10.72vw;
  height: 3.76vw;
  border-radius: 0.67vw;
  background: #1f1d2b;
  padding-left: 0.67vw;
  padding-right: 0.67vw;

  border: 0.083vw solid #393c49;
  justify-content: space-between;
}
.dashboroat_order > div > img {
  width: 1.67vw;
  height: 1.67vw;
}
.dashboroat_order > div > input {
  border: none;
  outline: none;
  padding-left: 0.6vw;
  padding-right: 0.67vw;
  font-style: normal;
  height: 2.01vw;
  width: 8vw;
  font-weight: 500;
  background: transparent;
  font-size: 1.4vw;
  color: white;
}
.dashboroat_order > div > input::placeholder {
  font-style: normal;
  font-weight: 500;
  font-size: 1.3vw;
  color: white;
}
.order_list {
  padding-left: 2.01vw;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  height: 1.67vw;

  list-style: none;
  width: 52vw;
  margin-top: 1.34vw;
}
.order_list > li {
  font-style: normal;
  font-weight: 600;
  font-size: 1.17vw;
  color: white;
}
.order_list > li:nth-child(2) {
  margin-left: 3.4vw;
}
.order_list > li:nth-child(3) {
  margin-left: 4.5vw;
}
.order_list > li:nth-child(4) {
  margin-left: 5vw;
}
.order-line {
  margin-top: 1.34vw;
  width: 54.1vw;
  border: 0.083vw solid #393c49;
}
