.setting_btn_box {
  display: flex;
  justify-content: space-between;
  margin-top: 4.02vw;
  width: 29.48vw;
}
.setting_btn_box > button {
  width: 14.4vw;
  height: 4.02vw;

  border-radius: 0.67vw;
  outline: none;
  border: none;
  font-weight: 600;
  font-size: 1.17vw;
  transition: all 0.1s linear;
}
.setting_btn_box > button:nth-child(2):hover {
  color: #ea7c69;
  background-color: white;
}
.setting_btn_box > button:nth-child(1):hover {
  color: white;
  background-color: #ea7c69;
  box-shadow: 0vw 0.67vw 2.01vw rgba(234, 124, 105, 0.4);
}
.setting_btn_box > button:nth-child(1) {
  color: #ea7c69;
  background-color: white;
}

.setting_btn_box > button:nth-child(2) {
  background: #ea7c69;
  box-shadow: 0vw 0.67vw 2.01vw rgba(234, 124, 105, 0.4);
  color: white;
}
