.form_submit {
  width: 29.9vw;
  display: flex;
  justify-content: space-between;
  margin-top: 7.4vw;
}
.form_submit > div {
  border: none;

  background: #ea7c69;
  color: white;
  cursor: pointer;

  box-shadow: 0vw 0.67vw 2.01 rgba(234, 124, 105, 0.4);
}
.form_submit > div,
.form_submit > button {
  width: 14.65vw;
  height: 4.02vw;
  outline: none;
  font-style: normal;
  font-weight: 600;
  font-size: 1.17vw;
  display: block;
  border-radius: 0.67vw;
  transition: all 0.2s linear;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form_submit > div:hover,
.form_submit > button {
  border: 0.083vw solid #ea7c69;
  background: transparent;
  color: #ea7c69;
}
.form_submit > button:hover {
  border: none;
  background: #ea7c69;
  color: white;

  box-shadow: 0vw 0.67vw 2.01 rgba(234, 124, 105, 0.4);
}
