.form {
  margin-top: 1.67vw;
}
.form_name > label {
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  display: block;
  color: #ffffff;
  margin-top: 1.67vw;
}
.form_name > input {
  width: 29.89vw;
  height: 4.02vw;

  background: #2d303e;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  outline: none;
  margin-top: 0.67vw;
  padding-left: 1.17vw;
display: block;
  font-weight: 400;
  font-size: 1.38vw;

  color: #e0e6e9;
}
.form_name > input:focus {
  border: 0.083vw solid #e0e6e9;
}
.form_data_box {
  display: flex;
  margin-top: 2.01vw;
}
.form_order > .form_table > input,
.form_data_box > .form_cvv > input,
.form_data_box > .form_data > input {
  width: 14.4vw;
  height: 4.02vw;
  margin-top: 0.67vw;
  background: #2d303e;
  outline: none;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  font-size: 1.3vw;
  font-weight: 400;
  display: block;
  padding-left: 1.17vw;
  padding-right: 1.17vw;
  color: #e0e6e9;
}
.form_order > .form_table > input:focus,
.form_data_box > .form_cvv > input:focus,
.form_data_box > .form_data > input:focus {
  border: 0.083vw solid #e0e6e9;
}
.form_data_box > .form_cvv {
  padding-left: 1.08vw;
}
.form_data_box > .form_cvv > label,
.form_data_box > .form_data > label {
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;
  display: block;
  
  color: #ffffff;
}
.form_line {
  width: 29.89vw;
  margin-top: 1.34vw;

  border: 0.083vw solid #393c49;
}
.form_order {
  margin-top: 1.34vw;
  display: flex;
}
.form_order > .form_table > label,
.form_order > .form_select > label {
  font-weight: 500;
  font-size: 1.17vw;
  display: block;
  line-height: 130%;
  display: block;
  color: #ffffff;
}
.form_select > select {
  width: 14.4vw;
  outline: none;
  height: 4.02vw;

  background: #1f1d2b;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4vw;
  color: white;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  appearance: none;
  margin-top: 0.67vw;
  background-image: url(/src/Restaran/Dashboroat/image/arrow-ios-down.svg);
  background-repeat: no-repeat;

  background-position: 8%;
  background-size: 15%;
  padding-left: 3vw;
  display: block;
}
.form_order > .form_table {
  padding-left: 1.08vw;
}
