.praduct_cart_box {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 1.34vw;
  margin-top: 2.01vw;
  height: 37.68vw;
  overflow-y: scroll;
}
.praduct_cart {
  transition: all 0.2s linear;
}
.praduct_cart_box::-webkit-scrollbar {
  width: 0.73vw;
  border-radius: 0.73vw;
}
.praduct_cart_box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0.41vw white;

  border-radius: 0.73vw;
}
.praduct_cart_box::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 0.73vw;
}

.praduct_cart_box > .praduct_cart {
  background: #1f1d2b;
  width: 18vw;
  height: 25.04vw;

  position: relative;
  border: 0.08vw solid #393c49;
  border-radius: 0.67vw;
}
.praduct_card_summa,
.praduct_card_title,
.praduct_card_image {
  display: flex;
  justify-content: center;
}
.praduct_cart:nth-child(1):hover {
  box-shadow: inset 0 0 0.41vw #ea7c69;
}

.praduct_cart:nth-child(1) {
  display: flex;

  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 0.083vw dashed #ea7c69;
}
.praduct_card_image > img {
  width: 10.63vw;
  height: 10.63vw;
  display: block;
  margin-top: 2.01vw;
}

.praduct_card_title > div {
  width: 12.06vw;
  margin-top: 1.34vw;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;

  text-align: center;

  color: #ffffff;
}
.praduct_item_text {
  margin-top: 0.67vw;
  width: 9.38vw;
  height: 1.67vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.praduct_item_text > #cracle {
  width: 0.35vw;
  height: 0.35vw;
  background: #c4c4c4;
  border-radius: 50%;
}
.praduct_item_text > #summa,
.praduct_item_text > #text {
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;
  line-height: 140%;

  color: #abbbc2;
}
.praduct_card_btn {
  background: #ea7c6969;

  width: 100%;
  height: 4.35vw;
  border-radius: 0vw 0vw 0.67vw 0.67vw;
  margin-top: 1.31vw;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.praduct_card_btn > img {
  width: 1.67vw;
  height: 1.67vw;
}
.praduct_card_btn > p {
  padding-left: 0.67vw;
  font-style: normal;
  font-weight: 600;
  font-size: 1.17vw;
  /* color: white; */

  color: #ea7c69;
}

.praduct_cart:nth-child(1) > .praduct_card_btn {
  display: none;
}
.praduct_cart:nth-child(1) > .praduct_card_summa > .praduct_item_text {
  display: none;
}
.praduct_cart:nth-child(1) > .praduct_block {
  width: 100%;
  text-align: center;
  cursor: pointer;
}
.praduct_cart:nth-child(1):hover > .praduct_block > .praduct_block_plus,
.praduct_cart:nth-child(1):hover > .praduct_block > .praduct_block_add {
  color: #ea7c697c;
}

.praduct_cart:nth-child(1) > .praduct_block > .praduct_block_add,
.praduct_cart:nth-child(1) > .praduct_block > .praduct_block_plus {
  color: #ea7c69;
  text-align: center;
}
.praduct_cart:nth-child(1) > .praduct_block > .praduct_block_plus {
  font-size: 2.01vw;
}
.praduct_cart:nth-child(1) > .praduct_block > .praduct_block_add {
  font-size: 1.34vw;
  font-weight: 600;
  margin-top: 2.01vw;
}
.praduct_cart:hover {
  box-shadow: inset 0 0 0.41vw white;
}
