.setting_filter {
  display: flex;
  height: 4.02vw;
  align-items: center;
  justify-content: space-between;
}
.setting_filter > h2 {
  color: white;
  font-style: normal;
  font-weight: 600;
  font-size: 1.67vw;
  line-height: 140%;
}
.setting_filter > .filter_item {
  width: 14.9vw;
  height: 4.02vw;

  display: flex;
  align-items: center;
  padding-left: 1.1vw;
  padding-right: 1.1vw;

  border: 0.083vw solid #393c49;
  filter: drop-shadow(0vw 0.67vw 2.01vw rgba(146, 136, 224, 0.3));
  border-radius: 0.67vw;
}
.setting_filter > .filter_item > img {
  width: 1.67vw;
  height: 1.67vw;
}
.setting_filter > .filter_item > input {
  padding-left: 0.67vw;
  outline: none;
  border: none;
  width: 11vw;
  height: 2.5vw;
  background-color: transparent;
  font-weight: 600;
  font-size: 1.17vw;
  color: #ffffff;
}
.setting_filter > .filter_item > input::placeholder {
  color: white;
}
