.list {
  width: 40.28vw;
  display: flex;
  justify-content: space-between;
  list-style-type: none;

  margin-top: 2.01vw;
}
.list_avtive {
  color: red;
}
.filter_item {
  cursor: pointer;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;
  display: block;
  text-decoration: none;
  color: #ffffff;
  position: relative;
  transition: all 0.1s linear;
}
.filter_item.focus {
  color: #ea7c69;
}

.filter_item:focus::before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 2.4vw;
  width: 4vw;
  height: 0.2vw;
  border-radius: 0.67vw;
  background-color: #ea7c69;
}
.filter_item_active {
  color: blue;
}

.link_active {
  height: 0;
  border: 0.08vw solid rgba(18, 17, 17, 0.127);
  border-radius: 5vw;
  margin-top: 1.08vw;
}
