.pointer_line {
  width: 31.1vw;

  margin-top: 1.34vw;
  background: #1f1d2b;

  border: 0.083vw solid #393c49;
}
.pointer_image > img {
  width: 14.7vw;
  height: 14.7vw;
}
.pointer {
  display: flex;
  padding: 2.6vw 2.01vw 2.01vw 2.01vw;
}
.pointer_text {
  display: flex;
  padding-top: 1.42vw;
}
.pointer_text > .color {
  width: 1.5vw;
  height: 1.5vw;
  border-radius: 50%;
}
.pointer_text > .color_text {
  padding-left: 0.75vw;
}
.pointer_text > .color_text > :nth-child(1) {
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;

  color: #ffffff;
}
.pointer_text > .color_text > :nth-child(2) {
  font-weight: 400;
  font-size: 1.005vw;
  line-height: 140%;

  color: #abbbc2;
}
