.dashbort {
  list-style: none;
  display: grid;
  padding-left: 2.01vw;
  padding-right: 2.01vw;
  margin-top: 1.34vw;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}
.dashbort > li:nth-child(1) {
  display: flex;
  width: 11.55vw;
  height: 2.68vw;
  align-items: center;
}
.dashbort > li:nth-child(1) > img {
  width: 2.68vw;
  height: 2.68vw;
}
.dashbort > li:nth-child(1) > p {
  padding-left: 1.28vw;
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;
  color: #e0e6e9;
}
.dashbort > li:nth-child(2) {
  padding: 0;
  margin-left: 4.27vw;

  width: 11.47vw;
}
.dashbort > li:nth-child(2) > p {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 1.17vw;
  line-height: 140%;
  color: #e0e6e9;
}
.dashbort > li:nth-child(3) {
  padding-left: 2.68vw;
}
.dashbort > li:nth-child(3) > p {
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;
  color: #e0e6e9;
}
.dashbort > li:nth-child(4) {
  margin-left: 10.05vw;
  cursor: pointer;
}

.completed {
  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(107, 226, 190, 0.24);
  border-radius: 2.51vw;
  height: 2.17vw;
  width: 7.62vw;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  transition: all 0.2s linear;
  color: #50d1aa;
}
.completed:hover {
  background-color: #50d1aa;
  color: white;
}
.pending:hover {
  background-color: #ffb572;
  color: aliceblue;
}
.pending {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s linear;
  background: rgba(255, 181, 114, 0.2);
  border-radius: 2.51vw;
  height: 2.17vw;
  width: 7.62vw;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;

  color: #ffb572;
}
.preparing:hover {
  background-color: #9290fe;
  color: white;
}
.preparing {
  border-radius: 2.51vw;
  height: 2.17vw;
  width: 7.62vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  color: #9290fe;
  background: rgba(146, 144, 254, 0.2);
}
