.selection {
  display: flex;
  height: 4.02vw;
  align-items: center;
  justify-content: space-between;
  margin-top: 2.28vw;
}
.selection > h2 {
  font-family: 'Barlow', sans-serif;
  font-size: 1.67vw;
  font-weight: 600;
  color: white;
}
.selection > select {
  height: 4.02vw;
  width: 8.37vw;
  background: #1f1d2b;
  border: 0.083vw solid #393c49;
  outline: none;
  padding: 1.17vw;
  color: white;

  font-family: 'Barlow', sans-serif;
  font-size: 1.17vw;
  font-weight: 500;
  border-radius: 0.67vw;
}
.selection > select > option {
  width: 8.37vw;

  padding: 1.17vw;

  font-family: 'Barlow', sans-serif;
  font-size: 1.17vw;
  font-weight: 500;

  background: #1f1d2b;
}
