.confirmation {
  height: 69.8vw;
  width: 69.8vw;
  background: #1f1d2b;
  border-radius: 1.34vw 1.34vw 0vw 1.34vw;
  position: absolute;
  display: flex;
  top: -69.8vw;
  right: 0;
}
.confirmation_left {
  padding: 2.01vw;
  width: 34.9vw;
  border-right: 0.083vw solid #393c49;
}
.confirmation_icon_luft {
  color: white;
  font-size: 1.5vw;
  display: block;
  transform: scale(1.2);
  transition: all 0.2s linear;
}
.confirmation_icon_luft:hover {
  color: red;
  cursor: pointer;
  transform: scale(1.5);
}
.confirmation_left > .title_top {
  justify-content: space-between;
  align-items: center;
  height: 5vw;
  display: flex;
  margin-top: 2.01vw;
  padding-bottom: 2vw;
}
.confirmation_left > .title_top > .title_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4.2vw;
  height: 4.2vw;
  background: #ea7c69;
  border-radius: 0.67vw;
}
.confirmation_left > .title_top > .title_icon > img {
  width: 1.67vw;
  height: 1.67vw;
}
.confirmation_right {
  padding: 2.01vw;
}
.confirmation_right_title {
  margin-top: 1.8vw;
  display: block;
}
.payment_title-botton_line {
  height: 0vw;
  width: 29.9vw;
  margin-top: 2.01vw;
  border: 0.083vw solid #393c49;
}
.confirmation_active {
  position: absolute;
  right: -100vw;
}
.block {
  transform: scale(0);
  transition: all 0.3s linear;
}
.none {
  transform: scale(1);
  transition: all 0.3s linear;
}
