.selected_button > button {
  width: 30.23vw;
  height: 4.02vw;
  display: block;
  background: #ea7c69;
  box-shadow: 0vw 0.67vw 2.01vw rgba(234, 124, 105, 0.3);
  border-radius: 0.67vw;
  font-family: 'Barlow', sans-serif;
  border: none;
  outline: none;
  font-weight: 600;
  font-size: 1.34vw;
  border: 0.083vw solid wheat;
  color: #fafafa;
  cursor: pointer;
  transition: all 0.2s linear;
}
.selected_button > button:hover {
  color: #ea7c69;
  border: 0.083vw solid #ea7c69;
  background-color: #fafafa;
}
.selected_button {
  margin-top: 3.51vw;
}
