.order_name {
  display: flex;
  height: 3.76vw;
  align-items: center;
  justify-content: space-between;
}
.order_name > h2 {
  color: white;
  font-style: normal;
  font-weight: 600;
  font-size: 1.67vw;
}

.order_name > select {
  width: 8.2vw;
  outline: none;
  height: 3.76vw;

  background: #1f1d2b;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4vw;
  color: white;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  appearance: none;
  background-image: url(../image/arrow-ios-down.svg);
  background-repeat: no-repeat;
  background-position: 8%;
  background-size: 24%;
  padding-left: 3vw;
}
.order_name > select > option {
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  color: white;
}
