.orders_box > h2 {
  font-family: 'Barlow', sans-serif;
  font-size: 1.67vw;
  color: white;
  font-weight: 600;
}
.order_btn {
  width: 17.92vw;
  display: flex;
  margin-top: 2.01vw;
  justify-content: space-between;
}
.order_btn > button {
  padding: 0.58vw 1.005vw;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 1.17vw;
  cursor: pointer;
  outline: none;

  color: #ea7c69;
  background: #1f1d2b;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  transition: all 0.2s linear;
}
.order_btn > button:focus {
  background: #ea7c69;
  color: #ffffff;
  border: 0.083vw solid #ea7c69;
}

.order_title {
  margin-top: 2.01vw;
  display: flex;
  justify-content: space-between;
}
.order_title > div {
  width: 8.71vw;
  display: flex;
  justify-content: space-between;
}
.order_title > p,
.order_title > div > p {
  font-family: 'Barlow', sans-serif;
  font-size: 1.34vw;
  font-weight: 600;
  color: white;
}
