.selected_foot {
  display: flex;
  justify-content: space-between;
  margin-top: 2.01vw;
}
.selected_left {
  display: flex;
}
.selected_left > img {
  width: 3.35vw;
  height: 3.35vw;
}

.selected_summa {
  padding-left: 0.67vw;
}
.selected_summa > div {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;
  color: #ffffff;
}
.selected_summa > p {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.005vw;
  line-height: 140%;

  color: #abbbc2;
}
.selected_right {
  width: 9.38vw;
  height: 3.35vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.selected_right > div {
  width: 4.02vw;
  height: 4.02vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.34vw;
  color: #ffffff;

  background: #2d303e;

  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
}
.selected_right > p {
  font-family: 'Barlow', sans-serif;
  color: #ffffff;
  font-weight: 500;
  font-size: 1.34vw;
}
.selected_comment {
  display: flex;
  padding-top: 0.83vw;
  justify-content: space-between;
}
.selected_comment > input {
  width: 24vw;
  height: 4.02vw;
  background: #2d303e;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  padding-left: 1.17vw;
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.3vw;
  border: none;
  outline: none;
  color: white;
  padding-right: 2vw;
}
.selected_comment > input::placeholder {
  font-family: 'Barlow', sans-serif;
  font-size: 1.17vw;
  font-weight: 400;
  color: #889898;
}
.selected_comment > button {
  width: 4.02vw;
  background: rgba(0, 0, 0, 1);
  outline: none;

  height: 4.02vw;

  border: 0.083vw solid #ea7c69;
  transition: all 0.2s linear;
  border-radius: 0.67vw;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.selected_comment > button:hover {
  background-color: white;
}
.selected_comment > button > img {
  width: 1.67vw;
  height: 1.67vw;
}
.selected_box {
  padding-top: 1vw;
  border-top: 0.083vw solid #393c49;
  border-bottom: 0.083vw solid #393c49;
  height: 37vw;
  padding-right: 0.8vw;
  overflow-y: scroll;
  margin-top: 2.01vw;
}
.selected_box::-webkit-scrollbar {
  width: 0.73vw;
  border-radius: 4vw;
}
.selected_box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0.41vw white;
  border-radius: 4vw;
}
.selected_box::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 4vw;
}
.selected_total,
.selected_discount {
  display: flex;
  margin-top: 2.01vw;
  justify-content: space-between;
}
.selected_total > div,
.selected_discount > div {
  font-family: 'Barlow', sans-serif;
  font-size: 1.17vw;
  font-weight: 400;
  color: #abbbc2;
}
.selected_total > p,
.selected_discount > p {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.34vw;
  color: white;
}
