@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@500;600&family=Heebo:wght@500&family=Inter&family=Jost&family=M+PLUS+Rounded+1c:wght@500&family=Merriweather+Sans:wght@600&family=Montserrat:wght@300;500&family=Noto+Sans&family=Oleo+Script&family=Play:wght@700&family=Poppins:wght@500;600&family=Quicksand&family=Raleway:wght@500&family=Roboto:wght@400;500&family=Rubik:wght@500&family=Signika:wght@700&family=Water+Brush&display=swap');

.navbar {
  display: flex;
  height: 5.44vw;
  align-items: center;
  justify-content: space-between;
}
.navbar > .navbar_title > h1 {
  color: #fff;

  font-family: 'Barlow', sans-serif;
  font-size: 2.3vw;
  padding-top: 0;
  display: block;
  margin-top: 0;

  font-weight: 600;
}
.navbar > .navbar_title > p {
  font-family: 'Barlow', sans-serif;
  font-size: 1.34vw;
  font-weight: 400;
  color: #e0e6e9;
}
.navbar > .navbar_serach {
  width: 18.4vw;
  height: 4.02vw;
  display: flex;
  align-items: center;
  background: #2d303e;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  justify-content: space-between;
  padding: 0 0.7vw 0 0.8vw;
}
.navbar > .navbar_serach > .input_search {
  color: white;
  cursor: pointer;
  display: block;
  font-size: 1.67vw;
}
.navbar > .navbar_serach > input {
  height: 2.5vw;
  background: none;
  font-family: 'Barlow', sans-serif;
  border: none;
  font-size: 1.17vw;

  color: #abbbc2;
  outline: none;
  width: 13.73vw;
}
.navbar > .navbar_serach > input::placeholder {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.17vw;

  color: #abbbc2;
}
