.setting_sidebar {
  height: 60.55vw;
  width: 23.03vw;
  margin-top: 2.01vw;
  margin-left: 0.7vw;
  background: #1f1d2b;
  border-radius: 0.67vw;
  padding-top: 0.67vw;
}

.setting_Link_item {
  height: 7.28vw;
  width: 23.03vw;
  padding: 2.01vw;
  display: flex;
  position: relative;
}
.svgActiveClass {
  display: block;
  width: 1.34vw;
  height: 1.34vw;
}

.setting_icon_text {
  padding-left: 0.67vw;
}
.setting_icon_text > p:nth-child(1) {
  font-weight: 500;
  font-size: 1.17vw;
  line-height: 130%;
  color: #ffffff;
}
.setting_icon_text > p:nth-child(2) {
  font-weight: 400;
  font-size: 1.005vw;
  line-height: 140%;

  color: #abbbc2;
}

.setting_navlink {
  text-decoration: none;
}
.setactive {
  background: red;
}
.setting_navlink.active > .setting_Link_item {
  background: #ea7c6942;
}
.setting_navlink.active > .setting_Link_item::after {
  content: ' ';
  position: absolute;
  width: 0.33vw;
  height: 3.26vw;
  background: #ea7c69;
  border-radius: 2.51vw;
  transform: rotate(90);
  right: 0vw;
}
.setting_navlink.active
  > .setting_Link_item
  > .setting_icon_text
  > p:nth-child(1) {
  color: #ea7c69;
}
.setting_navlink.active > .setting_Link_item > svg path {
  fill: #ea7c69 !important;
  /* stroke: #ea7c69 !important; */
}
