.dashboroat_image_foot {
  display: flex;
  margin-top: 2vw;
}
.dashboroat_image_foot > .image > img {
  height: 4.51vw;
  display: block;
  width: 4.51vw;
}
.dashboroat_image_foot > .title {
  padding-left: 1.34vw;
}
.dashboroat_image_foot > .title > p:nth-child(1) {
  font-style: normal;
  font-weight: 500;
  font-size: 1.17vw;

  color: #e0e6e9;
}
.dashboroat_image_foot > .title > p:nth-child(2) {
  font-style: normal;
  font-weight: 400;
  margin-top: 0.35vw;
  font-size: 1.005vw;

  color: #abbbc2;
}
.foot_line {
  margin-top: 1vw;
  background: #393c49;
  border-radius: 1vw;
  display: block;
  border: 0.083vw solid #393c49;
}
