.dashboroat_Btn {
  margin-top: 2.4vw;
}
.dashboroat_Btn > button {
  width: 27.1vw;
  height: 4.02vw;
  display: block;
  outline: none;
  border: 0.083vw solid #ea7c69;
  border-radius: 0.67vw;
  font-style: normal;
  font-weight: 600;
  font-size: 1.17vw;
  color: #ea7c69;
  background: transparent;
  transition: all 0.2s linear;
}
.dashboroat_Btn > button:hover {
  background-color: #ea7c69;
  color: #1f1d2b;
  font-size: 1.3vw;
  border: 0.083vw solid white;
}
