.payment_compony {
  width: 26.8vw;
  display: flex;
  position: relative;
  justify-content: space-between;
}
#checkmark {
  position: absolute;
  left: 6.61vw;
  top: 1.8vw;
  width: 1.34vw;
  height: 1.34vw;
}
.payment_card > .payment_image {
  display: flex;
  justify-content: center;
}
.payment_card > .payment_image > img {
  width: 2.01vw;
  height: 2.01vw;
  margin-top: 0.83vw;
}
.payment_card {
  background: #1f1d2b;

  width: 8.45vw;
  margin-top: 1.34vw;
  height: 5.36vw;
  border: 0.083vw solid #393c49;
  border-radius: 0.67vw;
  transition: all 0.2s linear;
}
.payment_card:hover {
  background: linear-gradient(0deg, #252836, #252836),
    linear-gradient(0deg, #abbbc2, #abbbc2);
  border: 0.083vw solid #abbbc2;
}
.payment_card > p {
  text-align: center;
  font-weight: 500;
  font-size: 1.17vw;
  padding-top: 0.5vw;

  color: #abbbc2;
}
.payment_card:hover > p {
  color: white;
}
.payment_box > h2 {
  font-weight: 600;
  font-size: 1.67vw;
  margin-top: 2.01vw;
  color: #ffffff;
}
