.total_box {
  display: flex;
  justify-content: space-between;
  width: 54.1vw;
  margin-top: 2.01vw;
}
.total {
  background: #1f1d2b;
  border-radius: 0.67vw;
  width: 16.6vw;
  height: 11.97vw;
  padding: 1.34vw;
  transition: all 0.3s linear;
}
.total:hover {
  cursor: pointer;

  transform: translate(0.4vw, -0.3vw);
  box-shadow: 0vw 0vw 0.5vw rgba(255, 255, 255, 0.224);
}
.dollor_image {
  height: 3.18vw;
  width: 3.18vw;
  border-radius: 0.67vw;
  display: flex;
  align-items: center;
  justify-content: center;

  background: #252836;
}
.dollor_image > img {
  width: 2.01vw;
  height: 2.01vw;
}
.total_img {
  height: 3.18vw;
  width: 10.05vw;
  justify-content: space-between;
  display: flex;
  align-items: center;
}

.total_img > p {
  color: #50d1aa;

  font-weight: 500;
  font-size: 1.005vw;
}
.total_img > img {
  width: 1.507vw;
  height: 1.507vw;
}
.total_summa {
  margin-top: 1vw;
}
.total_summa > h2 {
  color: #ffffff;
  font-style: normal;
  font-weight: 600;
  font-size: 2.34vw;
}
.total_text {
  margin-top: 1vw;
}
.total_text > p {
  font-weight: 500;
  font-size: 1.17vw;

  color: #abbbc2;
}
